import React from "react"
import { GatsbySeo } from "gatsby-plugin-next-seo"
import ProductList from "globalOrganisms/ProductList"
import Layout from "globalLayoutsShopCategorySinglePage"

export default function ShopProductCategory(props) {
  return (
    <>
      <GatsbySeo
        title={
          props.pageContext.category !== undefined &&
          props.pageContext.category.title
        }
        description={
          props.pageContext.category !== undefined &&
          props.pageContext.category.description
        }
        canonical={`${props.location.href}`}
        openGraph={{
          url: props.location.href,
          title:
            props.pageContext.category !== undefined &&
            props.pageContext.category.title,
          description:
            props.pageContext.category !== undefined &&
            props.pageContext.category.description,
        }}
      />
      <Layout>
        <article>
          <h1>
            {props.pageContext.category !== undefined &&
              props.pageContext.category.title}
          </h1>
          <ProductList
            category={
              props.pageContext.category !== undefined &&
              props.pageContext.category.link
            }
            itemsLimit={5}
          ></ProductList>
        </article>
      </Layout>
    </>
  )
}
