import React from "react"
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import { Cart3 } from "react-bootstrap-icons"
import { useShopProducts } from "globalGraphqlStaticQueries/shop-all"
import BuyModal from "globalOrganisms/buyModal"
import * as S from "./styles"

export default function ProductList(props) {
  const shopProductsData = useShopProducts()
  function productAmountHandler(amount) {
    if (amount >= 1)
      return <div className="item-availability availabile">Есть в наличии</div>
    if (amount < 1)
      return <div className="item-availability unavailabile">Нет в наличии</div>
  }

  return (
    <S.ProductList itemsLimit={props.itemsLimit}>
      {shopProductsData.map(
        ({ node }) =>
          node.frontmatter.categories.indexOf(props.category) !== -1 && (
            <S.ListItem key={node.id}>
              <a
                href={`/${node.slug.replace(/content\//, "")}`}
                className="mb-8 d-block"
              >
                <GatsbyImage
                  alt={`${node.frontmatter.title} cover image`}
                  image={getImage(node.frontmatter.cover)}
                />
              </a>
              <a
                href={`/${node.slug.replace(/content\//, "")}`}
                className="item-title mb-8"
              >
                {node.frontmatter.title}
              </a>
              <div className="item-prices">
                <span className="item-old-price"></span>
                <div className="item-price">
                  <span>{`${node.frontmatter.extra.price} ₽`}</span>
                  <button
                    aria-label="product-buy-button"
                    data-bs-toggle="modal"
                    data-bs-target={`#${node.id.replace(
                      /[.,"*+!-?^${}()|[\]\\/]/gu,
                      ""
                    )}`}
                    className="element-medium element-button buy-button"
                  >
                    <Cart3 size={20} />
                  </button>
                  <BuyModal data={node}></BuyModal>
                </div>
              </div>
            </S.ListItem>
          )
      )}
    </S.ProductList>
  )
}
