import React from "react"
import * as S from "./styles"
import { CheckSquareFill } from "react-bootstrap-icons"
import getPaymentUrl from "globalLibs/payments/qiwi"
import Row from "globalAtoms/Row"
import ElementList from "globalOrganisms/ElementList"

export default function BuyModal(props) {
  const node = props.data
  const paymentProps = {
    clientEmail: "",
    productAmount: 1,
    productDiscount: 0,
  }

  function getProductPrice(price, discount, amount) {
    let priceWithDiscount = (price / 100) * discount
    let finalPrice = (price - priceWithDiscount) * amount
    return finalPrice
  }

  function onBuyFormSubmit(event) {
    event.preventDefault()
    if (paymentProps.clientEmail.indexOf("@") !== -1) {
      const paymentInfo = {
        amount: getProductPrice(
          node.frontmatter.extra.price,
          paymentProps.productDiscount,
          paymentProps.productAmount
        ),
        email: paymentProps.clientEmail,
        comment: ``,
      }
      location.href = getPaymentUrl(paymentInfo)
    }
  }

  return (
    <S.Modal
      className="modal fade"
      id={node.id.replace(/[.,"*+!-?^${}()|[\]\\/]/gu, "")}
      tabIndex="-1"
      aria-labelledby={node.id.replace(/[.,"*+!-?^${}()|[\]\\/]/gu, "")}
      aria-hidden="true"
    >
      <div className="modal-dialog modal-dialog-centered modal-small">
        <div className="modal-content">
          <header>
            <Row className="element-large">
              <div className="center-part">
                <span>Оформить заказ</span>
              </div>
            </Row>
          </header>
          <form
            onSubmit={event => {
              onBuyFormSubmit(event)
            }}
          >
            <div className="mt-24 mx-16">
              <Row className="element-medium mb-8">
                <input
                  type="email"
                  placeholder="Ваш email"
                  required
                  onChange={event => {
                    paymentProps.clientEmail = event.target.value
                  }}
                  className="left-part element-medium px-16"
                />
              </Row>
              {node.frontmatter.extra.amount > 1 && (
                <Row className="element-medium mb-8">
                  <input
                    type="text"
                    placeholder={`Количество товара (макс. ${node.frontmatter.extra.amount})`}
                    required
                    onChange={event => {
                      paymentProps.productAmount = event.target.value
                    }}
                    className="left-part element-medium px-16"
                  />
                </Row>
              )}
              <Row className="element-medium mb-8">
                <input
                  type="text"
                  placeholder="Промокод на скидку (не обязательно)"
                  onChange={event => {
                    if (
                      event.target.value.toUpperCase() === "NEWYEAR" ||
                      event.target.value.toUpperCase() === "WOTACC" ||
                      event.target.value.toUpperCase() === "KORBEN" ||
                      event.target.value.toUpperCase() === "JOVE" ||
                      event.target.value.toUpperCase() === "STRAIK" ||
                      event.target.value.toUpperCase() === "INSPIRER" ||
                      event.target.value.toUpperCase() === "BEOWULF" ||
                      event.target.value.toUpperCase() === "PROTANKI" ||
                      event.target.value.toUpperCase() === "AMWAY" ||
                      event.target.value.toUpperCase() === "VSPISHKA"
                    ) {
                      paymentProps.productDiscount = 10
                    }
                  }}
                  className="left-part element-medium px-16"
                />
              </Row>
            </div>
            <ElementList
              title="Способ оплаты"
              description={
                <span>
                  Нажимая на кнопку "Оплатить" вы соглашаетесь с{" "}
                  <a href="/rules" target="blank">
                    правилами магазина
                  </a>
                </span>
              }
              sectionClassName="mb-24"
              listClassName="theme-shadow-border-inset-y"
            >
              <Row className="element-medium mb-8">
                <div className="left-part px-16">
                  <i className="primary me-8">
                    <CheckSquareFill size={18} />
                  </i>
                  <span>Банковская карта или Qiwi</span>
                </div>
              </Row>
            </ElementList>
            <footer>
              <Row className="element-large">
                <button
                  type="button"
                  data-bs-dismiss="modal"
                  aria-label="Close"
                  className="element-button theme-shadow-border-inset-right"
                >
                  Отмена
                </button>
                <button type="submit" className="element-button fw-bold">
                  Оплатить
                </button>
              </Row>
            </footer>
          </form>
        </div>
      </div>
    </S.Modal>
  )
}
