import styled from "styled-components"

export const Modal = styled.div`
  header {
    box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
  }
  footer {
    display: flex;
    width: 100%;
    box-shadow: inset 0px 1px 0px ${({ theme }) => theme.separator_non_opaque};
    & button {
      width: 100%;
      height: 100%;
      color: ${({ theme }) => theme.tint_blue};
      border-radius: 0px;
    }
  }
`
