import styled from "styled-components"

export const ProductList = styled.div`
  display: grid;
  grid-template-columns: repeat(${props => props.itemsLimit || 5}, 1fr);
  margin-bottom: 64px;
  @media (max-width: 992px) {
    grid-template-columns: repeat(4, 1fr);
  }
  @media (max-width: 768px) {
    grid-template-columns: repeat(3, 1fr);
  }
  @media (max-width: 576px) {
    grid-template-columns: repeat(2, 1fr);
  }
`

export const ListItem = styled.div`
  padding: 16px;
  box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque},
    inset -1px 0px 0px ${({ theme }) => theme.separator_non_opaque};
  &:nth-of-type(${props => props.itemsLimit || 5}n) {
    box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
  }
  @media (max-width: 992px) {
    :nth-of-type(${props => props.itemsLimit || 5}n) {
      box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque},
        inset -1px 0px 0px ${({ theme }) => theme.separator_non_opaque};
    }
    :nth-of-type(4n) {
      box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
    }
  }
  @media (max-width: 768px) {
    :nth-of-type(4n) {
      box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque},
        inset -1px 0px 0px ${({ theme }) => theme.separator_non_opaque};
    }
    :nth-of-type(3n) {
      box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
    }
  }
  @media (max-width: 576px) {
    :nth-of-type(3n) {
      box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque},
        inset -1px 0px 0px ${({ theme }) => theme.separator_non_opaque};
    }
    :nth-of-type(2n) {
      box-shadow: inset 0px -1px 0px ${({ theme }) => theme.separator_non_opaque};
    }
  }
  & .item-title {
    display: flex;
    font-size: 15px;
    line-height: 20px;
    color: ${({ theme }) => theme.text_primary};
    text-decoration: none;
    height: 40px;
    overflow: hidden;
  }
  & .item-old-price {
    color: ${({ theme }) => theme.gray};
  }
  & .item-price {
    display: flex;
    width: 100%;
    align-items: center;
    justify-content: space-between;
    font-size: 22px;
    line-height: 28px;
    color: ${({ theme }) => theme.text_primary};
  }
  & .item-availability {
    font-size: 13px;
    line-height: 18px;

    & .availabile {
      color: ${({ theme }) => theme.tint_green};
    }
    & .unavailabile {
      color: ${({ theme }) => theme.tint_red};
    }
  }
  & .buy-button {
    display: flex;
    align-items: center;
    justify-content: center;
    color: ${({ theme }) => theme.tint_green};
  }
`
